import { BrowserRouter } from "react-router-dom";
import { HashLink as Link} from "react-router-hash-link"
import Logo from "../image/n.jpg"
let url="https://drive.google.com/file/d/1DDev4bDLA1AmDuyomIR_0jPF5ZGvOnmx/view?usp=sharing"
let resume="./fw18_0273_Nikhil_Walwatkar_ Resume.pdf"
export default function Nav_com(){
    const DownloadResume = () => {
  
        // using Java Script method to get PDF file
        fetch('fw18_0273_Nikhil_Walwatkar_ Resume.pdf').then(response => {
            response.blob().then(blob => {
                // Creating new object of PDF file
                const fileURL = window.URL.createObjectURL(blob);
                // Setting various property values
                let alink = document.createElement('a');
                alink.href = fileURL;
                alink.download = 'Nikhil-resume.pdf';
                alink.click();
            })
        })
       
    }
    return (
        <div className="MCom" >
        <div className="Con" >
            <div className="Logo_box" ><img src={Logo} alt="brand_logo" className="brand_logo" /></div>
                <BrowserRouter>
            <div className="navigations" >
                <Link to={"#home"} smooth ><p>Home</p></Link>
                <Link to={"#about"} smooth ><p>About</p></Link>
                <Link to={"#skill"} smooth ><p>Skills</p></Link>
                <Link to={"#project"} smooth ><p>Projects</p></Link>
                <Link to={"#contect"} smooth ><p>Contact</p></Link>
               <a href="https://drive.google.com/file/d/1DDev4bDLA1AmDuyomIR_0jPF5ZGvOnmx/view?usp=sharing" onClick={DownloadResume} target="blank"><p>Resume</p></a> 
         



            </div>
                </BrowserRouter>
        </div>
        <hr className="line"></hr>
    </div>
    )
}